import React, { Component } from 'react'
import {
  Table,
  Button
} from 'antd'
import Layout from '../../layouts/default'
import { request } from '../../lib'
import moment from 'moment'
import 'moment/locale/zh-cn'

moment.locale('zh-cn')

const site = 'http://pvjt9jmxp.bkt.clouddn.com/'

export default class AdminList extends Component {
  constructor (props) {
    super(props)

    this.state = {
      dataSource: [],
      loading: false,
      columns: [{
        title: 'Êµ∑Êä•',
        dataIndex: 'posterKey',
        key: 'posterKey',
        render: (text, record) => (<img width='150' src={site + record.posterKey} />)
      }, {
        title: 'ÂêçÂ≠ó',
        dataIndex: 'title',
        key: 'title'
      }, 
      {
        title: 'ËØÑÂàÜ',
        dataIndex: 'rate',
        key: 'rate'
      },
      {
        title: 'Êõ¥Êñ∞Êó∂Èó¥',
        dataIndex: 'meta',
        key: 'meta',
        render: (text, record) => (<span>{moment(record.meta.createdAt).fromNow(true)} Ââç</span>)
      },
      {
        title: 'Ë±ÜÁì£ ID',
        dataIndex: 'doubanId',
        key: 'doubanId'
      },
      {
        title: 'Á±ªÂûã',
        dataIndex: 'movieTypes',
        key: 'movieTypes',
        render: (text, record) => (
          record.movieTypes.map((it, i) => (
            <p key={i}>{it}</p>
          ))
        )
      },
      {
        title: 'Ê†áÁ≠æ',
        dataIndex: 'tags',
        key: 'tags',
        render: (text, record) => (
          record.tags.map((it, i) => (
            <p key={i}>{it}</p>
          ))
        )
      },
      {
        title: 'ÁÆÄË¶Å',
        key: 'summary',
        render: (text, record) => <p style={{padding: '5px', maxWidth: '800px'}}>{record.summary}</p>
      },
      {
        title: 'ËØ¶ÊÉÖ',
        key: '_id',
        render: (text, record) => <a href={`/detail/${record._id}`} target={'_blank'}>ËØ¶ÊÉÖ</a>
      },
      {
        title: 'Êìç‰Ωú',
        key: 'action',
        render: (text, record) => <Button type='danger' onClick={() => this._deleteMovie(record._id)}>Âà†Èô§</Button>
      }]
    }
  }

  componentDidMount () {
    this._getAllMovies()
  }

  _delete = () => {
    console.log('Âà†Èô§')
  }

  _toggleLoading = (status = false) => {
    this.setState({
      loading: status
    })
  }

  _getAllMovies = () => {
    request(this._toggleLoading)({
      method: 'get',
      url: `/admin/movie/list`
    }).then(res => {
      this.setState({
        dataSource: res
      })
    }).catch(() => {
      this.setState({
        dataSource: []
      })
    })
  }

  _deleteMovie = (id) => {
    request({
      method: 'delete',
      url: `/admin/movies?id=${id}`
    }).then(res => {
      this.setState({
        dataSource: res
      })
    }).catch(() => {
      this.setState({
        dataSource: []
      })
    })
  }

  render () {
    let { dataSource, columns } = this.state

    if (!dataSource || !dataSource.length) return null

    dataSource = dataSource.map((data, i) => {
      data.key = i

      return data
    })

    return (
      <Layout {...this.props}>
        <div className='flex-row full'>
          <div className='flex-1 scroll-y align-self-start'>
            <Table dataSource={dataSource} columns={columns} />
          </div>
        </div>
      </Layout>
    )
  }
}